.promo {
  margin-bottom: 210px;

  @media (min-width: $desktop-width) {
    margin-bottom: 62px;
  }
}

.promo::before {
  @media (min-width: $desktop-width) {
    content: "";
    position: absolute;
    background-color: #68b738;
    background-image: url("../img/index-background-desktop1@1x.jpg");
    width: 50%;
    height: 694px;
    background-size: cover;
    right: 0;
    top: 0;
    z-index: 9;
    opacity: 0.2;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/index-background-desktop1@2x.jpg");
    }
  }
}

.promo::after {
  @media (min-width: $desktop-width) {
    content: "";
    position: absolute;
    background-color: #68b738;
    width: 50%;
    height: 694px;
    right: 0;
    top: 0;
    z-index: 8;
  }
}

.promo__wrapper {
  position: relative;
  background-color: $green;

  @media (min-width: $tablet-width) {
    background-color: #ffffff;
  }

  @media (min-width: $desktop-width) {
    max-width: 1440px;
    width: 100%;
    padding: 0 110px;
    margin: 0 auto;
    background: none;
  }
}

.promo__wrapper::before {
  @media (max-width: 767px) {
    content: "";
    position: absolute;
    background-image: url("../img/index-background-mobile2@1x.jpg");
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover;
    width: 100%;
    height: 100%;
    opacity: 0.15;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/index-background-mobile2@2x.jpg");
    }
  }
}

.promo__header {
  margin: 0;
  padding: 0 16px;

  width: auto;
  height: 302px;
  margin-bottom: 137px;
  position: relative;

  @media (min-width: $tablet-width) {
    background-color: #ffffff;
    margin-bottom: 650px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
    padding-left: 60px;
    min-height: 500px;
  }
}

