.promo__heading {
  width: 290px;
  font-size: 36px;
  line-height: 1;
  font-weight: 400;
  text-align: center;
  color: #ffffff;
  margin: 0 auto;
  padding-top: 20px;
  margin-bottom: 25px;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    color: #000000;
    width: 550px;
    margin: 0;
    text-align: left;
    padding-left: 117px;
    margin-bottom: 41px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 41px;
    padding: 0;
  }
}

.promo__slogan {
  margin: 0;
  margin-bottom: 320px;
  font-size: 14px;
  line-height: 1;
  text-align: center;
  color: #ffffff;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    color: #000000;
    text-align: left;
    margin-bottom: 49px;
    padding-left: 117px;
    position: relative;
    z-index: 11;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
  }
}

.promo__program {
  margin: 0 auto;
  z-index: 5;
  margin-bottom: 22px;
  font-size: 14px;
  line-height: 1.3;
  color: #ffffff;
  padding-left: 20px;
  padding-right: 20px;

  @media (min-width: $tablet-width) {
    position: absolute;
    top: 330px;
    left: 112px;
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 330px;
    left: 190px;
  }
}

.promo__button {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;

  @media (min-width: $tablet-width) {
    width: auto;
    margin-left: 117px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.promo__can-wrapper {
  z-index: 11;
  position: relative;
}

.promo__logo {
  position: absolute;
  top: 165px;
  left: calc(50% - 140px);
  z-index: 10;

  @media (min-width: $tablet-width) {
    width: 709px;
    height: 609px;
    top: 215px;
    left: calc(50% - 355px);
  }

  @media (min-width: $desktop-width) {
    width: 552px;
    height: 449px;
    top: -80px;
    left: 450px;
  }
}
